let html = document.querySelector('#html');
let style = document.querySelector('#style');
let string = `/*ä½ å¥½ï¼Œæˆ‘å«é˜¿ç›ã€‚
 *æ¥ä¸‹æ¥ï¼Œæˆ‘æ¼”ç¤ºæˆ‘çš„å‰ç«¯åŠŸåº•ï¼Œ
 *é¦–å…ˆï¼Œå‡†å¤‡ä¸€ä¸ªdiv
 */
#div1{
    border:1px solid red;
    width:200px;
    height:200px;
}
/*æ¥ä¸‹æ¥ï¼Œæˆ‘æŠŠdivå˜æˆä¸€ä¸ªå…«å¦å›¾
 *é¦–å…ˆï¼ŒæŠŠdivå˜æˆä¸€ä¸ªåœ†
 */
#div1{
    border-radius: 50%;
    box-shadow: 0 0 3px rgba(0,0,0,0.5);
    border:none;
}
/*å…«å¦å›¾ï¼Œå¤ªæç”Ÿä¸¤ä»ª
 *ä¸€é»‘ä¸€ç™½
 */
#div1{
    background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 50%, rgba(0,0,0,1) 50%, rgba(0,0,0,1) 100%);
}
/*å½¢æˆä¸¤ä»ª*/
#div1::before{
    width:100px;
    height:100px;
    left:0;
    top:0;
    left:50%;
    transform:translateX(-50%);
    background: radial-gradient(circle, rgba(0,0,0,1) 0%, rgba(0,0,0,1) 25%, rgba(255,255,255,1) 25%, rgba(255,255,255,1) 100%);
    border-radius:50%;
}
#div1::after{
    width:100px;
    height:100px;
    left:0;
    bottom:0;
    left:50%;
    transform:translateX(-50%);
    background: radial-gradient(circle, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 25%, rgba(0,0,0,1) 25%, rgba(0,0,0,1) 100%);
    border-radius:50%;
}
`;

let n = 0;
let string2 = '';

let step = () => {
    setTimeout(() => {
        // å¦‚æœæ˜¯å›è½¦ï¼Œå°±æ¢è¡Œï¼›å¦‚æœä¸æ˜¯å›è½¦ï¼Œå°±ç»§ç»­
        // string2 += string[n] === '\n' ? '<br>' : string[n];
        if (string[n] === '\n') {
            string2 = string2 + '<br>';
        } else if (string[n] === " ") {
            string2 += "&nbsp;";
        } else {
            string2 += string[n];
        };
        if (n < string.length - 1) {
            // å¦‚æœä¸æ˜¯æœ€åä¸€ä¸ªï¼Œå°±ç»§ç»­
            n += 1;
            step();
        };
        html.innerHTML = string2;
        style.innerHTML = string.substring(0, n);
        window.scrollTo(0,999);
        html.scrollTo(0,999);
    }, 50);

};
step();